#eventDetail {
  margin-bottom: 10%;
}

#attend {
  width: 9%;
  color: rgb(255, 255, 255); 
  font-size: 14px; 
  line-height: 14px; 
  padding: 4px; 
  border-radius: 19px; 
  font-family: Georgia, serif; 
  font-weight: normal; 
  text-decoration: none; 
  font-style: normal; 
  font-variant: normal; 
  text-transform: none; 
  background-image: 
  linear-gradient(to right, #0F142D 0%, #2D3A81 70%, #3F51B5 100%); 
  box-shadow: rgb(0, 0, 0) 2px 2px 10px 2px; 
  border: 2px solid #2D3A81; 
  display: inline-block;
  }
  #attend:hover {
  background: #1C6EA4; 
  border: 2px solid #1C6EA4;
  }
  #attend:active {
  background: #144E75; }